@import "main";
body{
  background: #fafafa;
}
*{
  line-height: 1.5rem;
}

h5{
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.75rem;
  color: rgba(5,5,5,1);
}

a {
  font-weight: 600;
}

.desktop-header{
  padding: 5px 8px 4px;
  position: fixed;
  top: -1px;
  left: 0px;
  right: 0px;
  z-index: 4;
  font-size: 14px;
  font-weight: 600;
  margin: 0px;
  justify-content: space-around;
  display: flex;
  @include respond-to('mobile-portrait') {
    display: none;
  }
  .header-container{
    display: grid;
    -webkit-box-pack: center;
    justify-content: center;
    width: 100%;
    justify-self: center;
    max-width: 968px;
    position: relative;
    grid-template-columns: repeat(6, max-content);
    z-index: 3;
  }
  .header-bg{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    z-index: -1;
    box-shadow: rgba(0, 0, 0, 0.06) 0px 1px 0px;
    backdrop-filter: saturate(180%) blur(20px);
    background-color: rgba(255,255,255,0.6);
  }
  .nav-label{
    display: flex;
    flex: 1 1 0%;
    z-index: 3;
    position: relative;
    a{
      padding: 8px 32px;
      border-radius: 4px;
      width: 100%;
      margin: 0px 1px;
      text-align: center;
      &:hover{
        cursor: pointer;
        background-color: rgba(59,130,246,0.1);
        color: rgba(59,130,246,1);
      }
    }
    &.active{
      a{
        background-color: rgba(59,130,246,0.1);
        color: rgba(59,130,246,1);
      }
    }
  }
}

#menu-mobile-header.hidden{
  display: none;
}
.grid-cols-2 {
  @include respond-to('mobile-portrait') {
    grid-template-columns: repeat(1,minmax(0,1fr)) !important;
  }

}

.mobile-header{
  display: none;
  padding: 5px 8px 4px;
  position: fixed;
  top: -1px;
  left: 0px;
  right: 0px;
  z-index: 4;
  font-weight: 600;
  margin: 0px;
  font-size: 16px;
  -webkit-box-pack: center;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  @include respond-to('mobile-portrait') {
    display: block;
  }
  .dropdown{
    display: none;
    &.show{
      display: block;
    }
  }
  .nav-label{
    display: flex;
    flex: 1 1 0%;
    z-index: 3;
    position: relative;
    a{
      padding: 12px;
      border-radius: 4px;
      width: 100%;
      margin: 0px 1px;
      text-align: left;
      &:hover{
        cursor: pointer;
        background-color: rgba(59,130,246,0.1);
        color: rgba(59,130,246,1);
      }
    }
    &.active{
      a{
        background-color: rgba(59,130,246,0.1);
        color: rgba(59,130,246,1);
      }
    }
  }
  .fUXgOT{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    z-index: -1;
    box-shadow: rgba(0, 0, 0, 0.06) 0px 1px 0px;
    backdrop-filter: saturate(180%) blur(20px);
    background-color: rgba(255,255,255,0.6);
  }
  .dEbpeE {
    display: block;
    font-size: 26px;
    font-weight: 300;
    cursor: pointer;
    z-index: 3;
    position: relative;
  }
  .cWklVx {
    display: flex;
    flex: 1 1 0%;
    z-index: 3;
    width: 100%;
    position: relative;
  }
}
h1{
  font-weight: 800;
  font-size: 2.25rem;
  line-height: 2.5rem;
  --tw-text-opacity: 1;
  color: rgba(5,5,5,1);
}
.btn-large {
  border-radius: .375rem;
  font-weight: 600;
  padding: .75rem 1.5rem;
}

.btn {
  border-radius: .25rem;
  border-width: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: .875rem;
  line-height: 1.25rem;
  padding: .5rem 1rem;
  --tw-shadow: 0 1px 2px 0 rgba(0,0,0,0.05);

  transition-property: box-shadow;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .15s;
  --tw-text-opacity: 1;
  background-color: rgba(255,255,255,1);
  color: rgba(5,5,5,1);
}
.btn-primary {
  border-style: none;
  color: rgba(255,255,255,1);
